.graf-circle:first-child {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%,-50%) rotateY(1000deg) rotateX(1000deg) rotate(0deg);
            transform: translate(-50%,-50%) rotateY(1000deg) rotateX(1000deg) rotate(0deg);
    width: 50px;
    height: 50px;
    border-radius: 50%;
    border: 5px solid #2098f3;
    -webkit-animation: scaleOne 5.5s infinite alternate linear;
            animation: scaleOne 5.5s infinite alternate linear;
    opacity: 0
}

