body{
    background-color: #7EDDDD;
  }
  .generallogin-box{
    position:relative;
    margin: 10% auto;
    width: 300px;
    height: 480px;
    background-color: #fff;
    padding: 2%;
    border-radius: 3px;
    -webkit-box-shadow: 0px 2px 3px 0px rgba(0,0,0,0.33);
  -moz-box-shadow: 0px 2px 3px 0px rgba(0,0,0,0.33);
  box-shadow: 0px 2px 3px 0px rgba(0,0,0,0.33);
  }
  .generallb-header{
    position:relative;
    color: #00415d;
    margin: 5px 5px 10px 5px;
    padding-bottom:10px;
    border-bottom: 1px solid #eee;
    text-align:center;
    height:28px;
  }
  .generallb-header a{
    margin: 0 25px;
    padding: 0 20px;
    text-decoration: none;
    color: #666;
    font-weight: bold;
    font-size: 15px;
    -webkit-transition: all 0.1s linear;
    -moz-transition: all 0.1s linear;
    transition: all 0.1s linear;
  }
  .generallb-header .active{
    color: #029f5b;
    font-size: 18px;
  }
  .social-login{
    position:relative;
    float: left;
    width: 100%;
    height:auto;
    padding: 10px 0 15px 0;
    border-bottom: 1px solid #eee;
  }
  .social-login a{
    position:relative;
    float: left;
    width:calc(40% - 8px);
    text-decoration: none;
    color: #fff;
    border: 1px solid rgba(0,0,0,0.05);
    padding: 12px;
    border-radius: 2px;
    font-size: 12px;
    text-transform: uppercase;
    margin: 0 3%;
    text-align:center;
  }
  .social-login a i{
    position: relative;
    float: left;
    width: 20px;
    top: 2px;
  }
  .social-login a:first-child{
    background-color: #DF4A32;
    margin-left: 80px ;
  }
  
  .generalusername-login,.generalusername-signup{
    position:relative;
    float: left;
    width: 100%;
    height:auto;
    margin-top: 20px;
    text-align:center;
  }
  .generalu-form-group{
    width:100%;
    margin-bottom: 10px;
  }
  .generalu-form-group input[type="text"],
  .generalu-form-group input[type="password"]{
    margin: auto;
    width: inherit;
    height:45px;
    outline: none;
    border: 1px solid #ddd;
    padding: 0 10px;
    border-radius: 2px;
    color: #333;
    font-size:0.8rem;
    -webkit-transition:all 0.1s linear;
    -moz-transition:all 0.1s linear;
    transition:all 0.1s linear;
  }
  .generalu-form-group input:focus{
    border-color: #358efb;
  }
  .generalu-form-group button{
    width:50%;
    height: auto;
    background-color: #1CB94E;
    border: none;
    outline: none;
    color: #fff;
    font-size: 14px;
    font-weight: normal;
    padding: 14px 0;
    border-radius: 2px;
    text-transform: uppercase;
  }
  .admin-btn button{
    width:50%;
    margin-top: 10px;
    background-color: #9d58bd;
    border: none;
    outline: none;
    color: #fff;
    font-size: 14px;
    font-weight: normal;
    padding: 14px 0;
    border-radius: 2px;
    text-transform: uppercase;
  }

  .myPortfolio {
      color: inherit;
      text-decoration: none;
      background:
        linear-gradient(currentColor, transparent, currentColor, transparent, currentColor, transparent, currentColor)
        repeat-x
        0 100% /* Bottom-aligned */
        /
        100% 3px; /* 100% width, 3px height */
      box-decoration-break: clone;
  }
    
    
  .myPortfolio {
     margin-top: 50px;
      font-size: 20px;
      font-weight: 300px;
      color: #00a39b;
    }
    
  .myPortfolio:hover,
  .myPortfolio:focus {
      color: #ffd200;
  }
